""
import contentpt from "@/assets/contentpt.json";
import contenten from "@/assets/contenten.json";
import { useLanguage } from "../header/LanguageContext"; 

function Cards() {
  const { language } = useLanguage();
  const content = language === "pt" ? contentpt : contenten;
  const item = content.homeCards.text;

  return (
    <div className="border-b mb-10 pb-10 border-(--horizontal-line)">
      <div className="px-5">
        {item.map((card, index) => (
          <div key={index} className="mb-10">
            <h1 className="font-bold text-(--orange) pb-5 text-[2em]">
              {card.pageTitle}
            </h1>

            <div className="m-auto flex flex-col md:flex-row max-w-450 bg-(--gray) items-center justify-center gap-10 rounded-[35px] h-200">
              <div className="p-5">
                <h2 className="text-xl font-bold">{card.title}</h2>
                <p>{card.subTitle}</p>
              </div>

              <div className="flex gap-5">
                {card.image && <img src={card.image} alt="" className="max-w-[200px]" />}
                {card.image2 && <img src={card.image2} alt="" className="max-w-[200px]" />}
              </div>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}

export default Cards;
